@import "../../../styles/styles.scss";

.TokenRow {
  display: flex;
  cursor: pointer;
  flex-direction: row;
  font-size: 20px;
  justify-content: space-between;
  margin: 0 0 33px 66px;
  min-height: 40px;

  &-Rates {
    display: flex;
    flex-direction: column;
  }

  &-Name {
    font-weight: bold;
  }

  &-Rate {
    color: grey;
    font-size: $fiatFontSize;
    text-shadow: none;
  }

  &-Amounts {
    display: flex;
    flex-direction: column;
  }

  &-TokenAmount {
    font-weight: bold;
  }

  &-FiatAmount {
    align-self: flex-end;
    color: grey;
    font-size: $fiatFontSize;
    text-shadow: none;
  }
}
